const reducerSection5 = (state = initialState, action = {}) => {
    switch(action.type){
        case UPDATE_ACTIVE: {
            return {
                ...state,
                active: action.index,
            };
        }
        default:
            return state;
    }
}

const UPDATE_ACTIVE = 'UPDATE_ACTIVE'

export const updateActiveCreator = (index) => ({
    type: UPDATE_ACTIVE,
    index: index,
});

export default reducerSection5;